.container {
  position: relative;

  display: grid;
  /* grid-template-columns: repeat(auto-fill, minmax(1fr)); */
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-flow: dense;

  /* grid-template-rows: auto; */
  grid-gap: 0 20px;
}
.box {
  margin-top: 20px;
  margin-bottom: 20px;
}
.container .box {
  position: relative;
  height: 400px;
  background: linear-gradient(-315deg, #181964, #0045a0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container .box:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
  pointer-events: none;
}

.content {
  position: relative;
  padding: 20px;
  transform: translateY(40px);
}
.content .main_h2 {
  position: absolute;
  top: -60px;
  right: 20px;
  margin: 0;
  padding: 0;
  font-size: 7.5em;
  color: rgba(255, 255, 255, 0.1);
  transition: 0.5s;
}
.box:hover .content .main_h2 {
  top: -140px;
}

.box .content h3 {
  margin: 0 0 10px;
  margin-bottom: 30px;
  padding: 0;
  font-size: 24px;
  font-weight: 500;
  color: #fff;
  font-weight: bold;
}

.box .content .main-p {
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 16px;
}

.box .content .link-main {
  position: relative;
  margin: 35px 0 0;
  padding: 10px 20px;
  text-decoration: none;

  display: inline-block;
  color: #fff;
  transition: 0.5s;
  transform: translateY(-20px);
  opacity: 0;
}

.box:hover .content .link-main {
  transform: translateY(0);
  opacity: 1;
  visibility: visibile;
}

.box .content .link-main:hover {
  color: rgb(32, 32, 32);
  background: #fff;
  /* background: linear-gradient(-90deg, #242424, #292929); */
}

.container .box:nth-child(1):before {
  background: linear-gradient(#32cffa, #0568ea);
}

hr {
  /* Убираем границу */
  background-color: #171718;
  /* Цвет линии */
  color: #171718;
  /* Цвет линии для IE6-7 */
  height: 0.5px;
  /* Толщина линии */
}
